
import Msg from 'vux/src/components/msg/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import {mapMutations,} from 'vuex'
export default {
name:'logout',
components: {
Msg,
XButton
},
data(){
return{
isShowMsg:false,
buttons:{
type:'primary',
text:'',
link:'/login'

}
}
},
created:function(){

if(false==this.$store.state.isLogined){
this.isShowMsg=true;
return;
}


this.$store.commit('mutationLogout');

window.localStorage.clear();


this.$router.push('/');


}
}



